/** BLL - Business Logic Layer 
 * describes the changes to the State for the Users page */import { APIResponseType } from '../components/api/api';
import { usersAPI } from '../components/api/users-api';
import { UserType } from '../types/types';
import { updateObjectInArray } from '../utils/object-helpers';
import { BaseThunkType, InferActionsTypes } from './redux-store';
import { Dispatch } from 'redux'


let initialState = {
	users: [] as Array<UserType>, 
	pageSize: 12,
	totalUsersCount: 0,
	currentPage: 1,
	isFetching: false,
	followingInProgress: [] as Array<number>, //массив userId
	filter: {
		term: "",
		friend: null as null | boolean
	}
};

export type InitialStateType = typeof initialState; //export for usage in "user-reducer.test.ts"
export type FilterType = typeof initialState.filter; //export for usage in "SaerchForm.tsx"




const usersReducer = (state = initialState, action : ActionsTypes): InitialStateType => {
	switch (action.type) {

		case 'USERS/FOLLOW':
			return {
				...state,
				users: updateObjectInArray(state.users, action.userId, "id", { followed: true }),
			}


		case 'USERS/UNFOLLOW':
			return {
				...state,
				users: updateObjectInArray(state.users, action.userId, "id", { followed: false }),
			};

		case 'USERS/SET_USERS':
			return {
				...state,
				// users : [...state.users, ...action.users]
				users: action.users
			};

		case 'USERS/SET_CURRENT_PAGE':
			return {
				...state,
				currentPage: action.currentPage
			};

			case 'USERS/SET_FILTER':
			return {
				...state,
				filter: action.payload
			};



		case 'USERS/SET_TOTAL_USERS_COUNT':
			return { ...state, totalUsersCount: action.totalUsersCount };

			case 'USERS/TOGGLE_IS_FETCHING':
			return { ...state, isFetching: action.isFetching };

		case 'USERS/TOGGLE_IS_FOLLOWING_PROGRESS':
			return {
				...state,
				followingInProgress: action.isFetching ?
					[...state.followingInProgress, action.userId] :
					state.followingInProgress.filter(id => id !== action.userId),
			};
		
		default:
			return (state);
	}
};
export default usersReducer;

/** ActionCreator  SET*/
/** ActionCreatorы for Post*/
/** We typify all "actions"*/
type ActionsTypes = InferActionsTypes<typeof actions>

/** pack all the ACs into the "actions" Object*/
export const actions = {
	/**Key (AC name): value (AC itself) */
	followSuccess : (userId: number) => ({ type: 'USERS/FOLLOW', userId } as const),
	unfollowSuccess : (userId: number) => {return { type: 'USERS/UNFOLLOW', userId } as const},
	setCurrentPage : (currentPage: number) => {return { type: 'USERS/SET_CURRENT_PAGE', currentPage: currentPage } as const},
	setFilter : (filter: FilterType) => {return { type: 'USERS/SET_FILTER', payload: filter } as const},
	setTotalUsersCount : (totalUsersCount: number) => {return { type: 'USERS/SET_TOTAL_USERS_COUNT', totalUsersCount: totalUsersCount } as const},
	toggleIsFetching : (isFetching: boolean) => {return { type: 'USERS/TOGGLE_IS_FETCHING', isFetching: isFetching } as const},
	toggleFollowingProgress : (isFetching: boolean, userId: number)=> {return { type: 'USERS/TOGGLE_IS_FOLLOWING_PROGRESS', isFetching, userId } as const},
	setUsers : (users: Array<UserType>) =>({ type: 'USERS/SET_USERS', users } as const),
}


/** ThunkCreators*/
/**to tipe ThunkCreator requestUsers*/
/** preparing typing for
 * "async (dispatch : DispatchType, getState : AppStateType ) => {"
 * inside requestUsers
 * { Dispatch } from 'redux'
*/
type DispatchType = Dispatch<ActionsTypes>  //для const "_followUnfollowFlow"

type ThunkType = BaseThunkType<ActionsTypes>  //Локальный "ThunkType" типизирован общим типом "BaseThunkType" с нашими "<ActionsTypes>"-ми


/** "requestUsers" is a Thunk-Creator (a subtype of ActionCreator). It cannot be simply called, only "dispatched"*/
export const requestUsers = (page: number, pageSize: number, filter : FilterType): ThunkType => {

	return (
		async (dispatch: DispatchType, getState) => {
			dispatch(actions.toggleIsFetching(true));
			dispatch(actions.setCurrentPage(page));
			dispatch(actions.setFilter(filter));

			let data = await usersAPI.requestUsers(page, pageSize, filter.term, filter.friend);
			dispatch(actions.toggleIsFetching(false));
			dispatch(actions.setUsers(data.items));
			dispatch(actions.setTotalUsersCount(data.totalCount));
		}
	);
}

/** "_followUnfollowFlow" - internal function - general part for consts "follow" and "unfollow*/

const _followUnfollowFlow = 			
	async (dispatch: DispatchType, 
			userId: number, 
			apiMethod: (userId:number) => Promise<APIResponseType>, 
			actionCreator: (userId: number) => ActionsTypes) => {
		dispatch(actions.toggleFollowingProgress(true, userId));  //"actions.toggleFollowingProgress(true, userId)" - наш объект
		let response = await apiMethod(userId);
		if (response.resultCode === 0) {
			dispatch(actionCreator(userId))
		};
		dispatch(actions.toggleFollowingProgress(false, userId));
	}

/** to type " follow ": ThunkType" */
export const follow = (userId: number): ThunkType => {
	return (
		async (dispatch) => {
			/** need "await" because there is an async function inside another async function*/
			await _followUnfollowFlow(dispatch, userId, usersAPI.follow.bind(usersAPI),actions.followSuccess);	
		}
	)
}

export const unfollow = (userId: number): ThunkType => {
	return (
		async (dispatch) => {
			await _followUnfollowFlow(dispatch, userId, usersAPI.unfollow.bind(usersAPI), actions.unfollowSuccess);
		}
	)
}

