/** BLL - Business Logic Layer 
 * describes the changes to the State for the Profile page */
import { ProfileAPI} from '../components/api/profile-api';
import { FormAction, stopSubmit } from 'redux-form';
import { PostType, ProfileType, PhotosType,  } from '../types/types';
import { BaseThunkType, InferActionsTypes } from './redux-store';


let initialState = {
	postsData: [
		{ id: 1, message: '123 Lorem ipsum dolor sit, amet consectetur adipisicing elit. Reprehenderit tempore ducimus perferendis veritatis distinctio consectetur facere natus doloribus dolores, pariatur aspernatur possimus consequuntur beatae incidunt necessitatibus eveniet accusantium alias aliquid.', likesCount: 1 },
		{ id: 2, message: '456 Lorem ipsum dolor sit, amet consectetur adipisicing elit. Reprehenderit tempore ducimus perferendis veritatis distinctio consectetur facere natus doloribus dolores, pariatur aspernatur possimus consequuntur beatae incidunt necessitatibus eveniet accusantium alias aliquid.', likesCount: 23 },
		{ id: 3, message: '789 Lorem ipsum dolor sit, amet consectetur adipisicing elit. Reprehenderit tempore ducimus perferendis veritatis distinctio consectetur facere natus doloribus dolores, pariatur aspernatur possimus consequuntur beatae incidunt necessitatibus eveniLorem ipsum dolor sit, amet consectetur adipisicing elit. Reprehenderit tempore ducimus perferendis veritatis distinctio consectetur facere natus doloribus dolores, pariatur aspernatur possimus consequuntur beatae incidunt necessitatibus eveniet accusantium alias aliquid.et accusantium alias aliquid.', likesCount: 78 }
	] as Array<PostType>,

	profile : null as ProfileType | null,
	status : "",
	// newPostText: "",
};

export type InitialStateType = typeof initialState;



const profileReducer = (state = initialState, action : ActionsType) : InitialStateType=>{
	switch(action.type){
		case 'PROFILE/ADD-POST':{

			let newPost = {
				id: 4, 
				message: action.newPostText, 
				likesCount: '0'
			};


			return {
				...state,
				postsData : [...state.postsData,newPost] as Array<PostType>,
			};

		}


		case 'PROFILE/SET-USER-PROFILE':{
			return {...state, profile : action.profile};
		}
		

		case 'PROFILE/SET-STATUS':{
			return {...state, status : action.status};
		}

		case 'PROFILE/SAVE_PHOTOS_SUCCESS':{
			/** Make a copy of profile and change photos to those in action */
			return {...state, profile : {...state.profile, photos : action.photos} as ProfileType};
		}
		default:
			return(state);
	}
}; 
export default profileReducer;



/** ActionCreator  Post*/
/**  1. Pack all ACs into 1 Object "actions"*/
export const actions = {
	/**  Key (AC name): value (AC itself)*/
	addPostActionCreator : (newPostText: string)=>{return {type : 'PROFILE/ADD-POST', newPostText} as const},
	setUserProfile : (profile: ProfileType)=>{return {type : 'PROFILE/SET-USER-PROFILE', profile}	as const},
	setStatus : (status: string) =>{return {type : 'PROFILE/SET-STATUS', status}as const},
	deletePost : (postId : number)=>{return {type : 'PROFILE/DELETE_POST', postId}	as const},
	savePhotoSuccess: (photos : PhotosType) =>{return {type : 'PROFILE/SAVE_PHOTOS_SUCCESS', photos} as const}
}
/** 2. All "actions"  are typed*/
type ActionsType = InferActionsTypes<typeof actions>	

/** 3. Thunks  are typed*/
export type ThunkType = BaseThunkType<ActionsType | FormAction>  //"FormAction" из 'redux-form'-мы, чтобы добавить в dispatch "Sabmit"//"FormAction" из 'redux-form'-мы, чтобы добавить в dispatch "Sabmit"



/** THUNKs*/
/** Request to obtain a user profile with a known id*/
export const getUserProfile = (userId : number) : ThunkType =>async (dispatch)=>{
	let data = await ProfileAPI.getProfile(userId)
		dispatch(actions.setUserProfile(data));
}

/** Request to get a profile of some User*/
export const getStatus = (userId : number) : ThunkType=>async(dispatch : any)=>{
	let data = await ProfileAPI.getStatus(userId)
		dispatch(actions.setStatus(data));
}
/** Request to receive Status update for us as a User*/
export const updateStatus = (status : string) : ThunkType=>async (dispatch)=>{
	try{
		let data = await ProfileAPI.updateStatus(status)
			if (data.resultCode === 0){
			dispatch(actions.setStatus(status));
			}
		}catch(error){
		}
}

/** save user's Photo*/
export const savePhoto = (file : File) : ThunkType => async (dispatch)=>{  //"File" из DOM-объекта
	let data = await ProfileAPI.savePhoto(file)
		if (data.resultCode === 0){
		dispatch(actions.savePhotoSuccess(data.data.photos));
		}
}

/** save user's  Profile*/
export const saveProfile = (profile : ProfileType) : ThunkType => async (dispatch, getState)=>{
	const userId = getState().auth.userId;
	let data = await ProfileAPI.saveProfile(profile)
		if (data.resultCode === 0){
			if(userId !=null){				//Если User есть
				dispatch(getUserProfile(userId));
			} else {
				throw new Error("userId can't be null");  //Вывести сообщение об ошибке, если нету User-а
			}
		}
		else{
			/** a general error will be shown*/
			dispatch(stopSubmit("edit-profile", {_error : data.messages[0]}));

			return Promise.reject (data.messages[0]);
		}
}