/** Main component of the project*/
import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';
import { connect, Provider } from 'react-redux';
import store, { AppStateType } from './redux/redux-store';
import { initializeApp } from "./redux/app-reducer";
import './App.css';
import Preloader from './components/common/Preloader/Preloader';
import AppAntD from './AppLayout';



/** Structure : (4 JSApp (3 AppContainer (2 App(1 AppAntD))))
 * 2 AppContainer supply (1 App)) with: "initialize"   and "initializeApp"
 * 3 JSApp supply (2 AppContainer) with: "BrowserRouter" and "store" 
*/


type MapPropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = {
  initializeApp: () => void
}

class App extends Component<MapPropsType & DispatchPropsType>{

  /** to catch the error
   * reasen-the reason for the error, promise-which of them failed*/
  catchAllUnhandledErrors = (e: PromiseRejectionEvent): any => {
    alert("Some error occurred")
  }


/** Request initial data from the server*/
/** componentDidMount is only triggered once when component is mounted*/
  componentDidMount() {
    this.props.initializeApp();

    /** We intercept all previously unintercepted promises*/
    window.addEventListener('unhandledrejection', this.catchAllUnhandledErrors)
  };

  /** If there is an addEventListener, then somewhere you need to do removeEventListener for it*/
  componentWillUnmount() {
    window.removeEventListener("unhandledrejection", this.catchAllUnhandledErrors);
  };

  render() {
    if (!this.props.initialized) {
      return <Preloader />
    }

    return (
      <AppAntD />
    );
  }
}



const mapStateToProps = (state: AppStateType) => ({
  initialized: state.app.initialized
});



let AppContainer = connect(mapStateToProps, { initializeApp })(App);


const JSApp: React.FC = () => {
  return (
    // BrowserRouter provides Routing when the project processes dynamic requests on the server.
    // HashRouter use when you have a static website
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      {/* Provider puts the store in the global CONTEXT so that everyone has access to it */}
      <Provider store={store}>
        <AppContainer />
      </Provider>
    </BrowserRouter>
  )
};

export default JSApp;

