/** Group of requests to the server for the UsersPage*/
import { GetItemsType, APIResponseType, instance } from './api';

/** Group of requests to the server for the User*/
export const usersAPI = {

	/** Get array of Users*/
	requestUsers(currentPage = 1, pageSize = 12, term: string = "", friend: null | boolean = null) {
		return (
			/** Typing all get, post, delete requests with the common type "GetItemsType" (we look at what Object the server returns and type it)*/
			instance.get<GetItemsType>(`users?page=${currentPage}&count=${pageSize}&term=${term }`+ (friend === null ? '' : `&friend=${friend}`))
				.then(Response => {
					return Response.data
				})
		)
	},

	/** Handle the "follow" state*/
	follow(userId: number) {
		return instance.post<APIResponseType>(`follow/${userId}`).then(res => res.data)
	},

	/** Handle the "unfollow" state*/
	unfollow(userId: number) {
		return instance.delete(`follow/${userId}`).then(res => res.data) as Promise<APIResponseType>
	},
}