/** Group of requests to the server for "login/logout/me-current user" */
import { instance, APIResponseType, ResultCodesCaptchaEnum, ResultCodesEnum } from './api';


/**Common type-Generic"APIResponseType" for all responses in api.ts */
/** Specify the general type "APIResponseType" for "MeResponse"*/
/** Type for "me()" in "authAPI" */
type MeResponseDataType = {  
	id: number   // Type for object "data: {id : number, email: string, login : string}
	email: string
	login: string
}

/** specify the general type "APIResponseType" for "LoginResponse"*/
type LoginResponseDataType = {   // Тип для "login()" в "authAPI"
	userId: number   // Тип для объекта data: {id : number, email: string, login : string}
	token: string
}


export const authAPI = {
	/** from HeaderContainer
	 * Typifying Promise "me"
	*/
	me() {
		return instance.get<APIResponseType<MeResponseDataType>>(`auth/me`).then(res => res.data);
	},
	login(email: string, password: string, rememberMe: boolean = false, captcha: null | string = null) {
		return instance.post<APIResponseType<LoginResponseDataType, ResultCodesEnum | ResultCodesCaptchaEnum>>(`auth/login`, { email, password, rememberMe, captcha })
			.then(res => {
				// authorization using a Bearer token
				instance.defaults.headers.Authorization = "Bearer " + res.data.data.token;
				return res.data
			});
	},
	logout() {
		return instance.delete(`auth/login`);
	}
}
