/** function for all reducers, which runs through the array and looks for matches and changes something in this Object.*/
export const updateObjectInArray = (items: any, itemsId: any, objPropName: any, newObjProps: any) =>{
	return items.map( (u: any)=>{
		if (u[objPropName] ===
			itemsId) {
			return{...u, ...newObjProps}
		};
		return u;
	})			
}



