/** BLL - Business Logic Layer 
 * describes the changes to the State for the Login page */
/** Here Thunks receive and process responses from the server
 *  and dispatch "actions" depending on the asynchronous response*/

import { FormAction, stopSubmit } from 'redux-form';
import { ResultCodesEnum, ResultCodesCaptchaEnum } from '../components/api/api';
import { authAPI } from '../components/api/auth-api';
import { securityAPI } from '../components/api/security-api';
import { BaseThunkType, InferActionsTypes } from './redux-store';


//const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
let initialState = {
	userId: null as number | null,
	email: null as string | null,
	login: null as string | null,
	isFetching: false as boolean,    
	isAuth: false,
	captchaUrl: null as string | null, 	// If captcha :null, then it will not be shown 
};
export type InitialStateType = typeof initialState;


/** describes the changes to the State for the Login form*/
const authReducer = (state = initialState, action: ActionsType): InitialStateType => {
	switch (action.type) {

		case 'auth/SET_USER_DATA':   
		case 'auth/GET_CAPTCHA_URL_SUCCESS':

			return {
				...state,
				/** Add to the end of the copied State the value that we will pull from action.payload (there is a captcha there) */
				...action.payload
			};

		default:
			return (state);
	}
};
export default authReducer;



/** ActionCreators*/
/** Set the User data when filling out the Login form*/

/**  all AC are typed*/
/**  1. Pack all ACs into 1 Object "actions"*/
export const actions = {
	setAuthUserData : (userId: number | null, email: string | null, login: string | null, isAuth: boolean) =>
	({ type: 'auth/SET_USER_DATA', payload: { userId, email, login, isAuth } } as const),   //Даем  названия const-ам в соотв-и с  Redux-Ducks "app/..."

	/** captcha*/
	getCaptchaUrlSuccess : (captchaUrl: string) => {
		return { type: 'auth/GET_CAPTCHA_URL_SUCCESS', payload: { captchaUrl } as const};
	},
}
/** 2. all "actions" are typed*/
type ActionsType = InferActionsTypes<typeof actions>	
/** 3. Thunks  are typed*/
/** Local type "ThunkType" is a generic type "BaseThunkType" with our "<ActionsType>"
 * "FormAction" from 'redux-form' - to add a dispatch in "Submit"
*/
type ThunkType = BaseThunkType<ActionsType | FormAction>  



/** THUNK Creators*/
/** Request for personal profile "me"*/
export const getAuthUserData = () : ThunkType => async (dispatch) => {
	let meDataResponse = await authAPI.me();

	if (meDataResponse.resultCode === ResultCodesEnum.Success) {
		let { id, email, login } = meDataResponse.data;
		dispatch(actions.setAuthUserData(id, email, login, true));
	};
};

/** Request for login*/
export const login = (email: string, password: string, rememberMe: boolean, captcha: string)  : ThunkType => //"ThunkType" им типизируем наш ThunkCreator
	async (dispatch) => {
	let loginData = await authAPI.login(email, password, rememberMe, captcha);

	if (loginData.resultCode === ResultCodesEnum.Success) {
		/** If we have successfully logged in, we receive additional data*/
		dispatch(getAuthUserData());
	}

	/** Stop Submit if there is an error*/
	else {
		if (loginData.resultCode === ResultCodesCaptchaEnum.CaptchaIsRequired) {
			dispatch(getCaptchaUrl());
		}
		/** messages[0] to take the zero message*/
		let message = loginData.messages.length > 0 ? loginData.messages[0] : "Some error"
		dispatch(stopSubmit("login", { _error: message }));
	}
};

/**Request to log out */
export const logout = () : ThunkType => async (dispatch: any) => {
	let response = await authAPI.logout();
	if (response.data.resultCode === ResultCodesEnum.Success) {
		dispatch(actions.setAuthUserData(null, null, null, false));
	}
}

/** Request for Captcha from the server (request is in "security-api.ts")
 * and then send the server's response to the "state (dispatch Action Creator with the received response)" */

/** it is ThunkCreator "export const getCaptchaUrl = ()=>"*/
export const getCaptchaUrl = () : ThunkType =>
/** Thunk*/
	async (dispatch: any) => {
		const response = await securityAPI.getCaptchaUrl();
		const captchaUrl = response.data.url;

		dispatch(actions.getCaptchaUrlSuccess(captchaUrl));
	};