/** Request to the server for Captcha*/
import {instance} from './api';

/** Group of requests to the server for Security(CAPTCHA)*/
/** Type the "get" request for "securityAPI"*/
type GetCaptchUrlResponseType ={
	url : string
}

export const securityAPI = {
	/** We came up with the name of the function ourselves and in it we send a request to the server for Captcha*/
	getCaptchaUrl() {
		return instance.get<GetCaptchUrlResponseType>(`security/get-captcha-url`);
	},
}