/** Group of requests to the server for ProfilePage */
import { PhotosType, ProfileType } from '../../types/types';
import { APIResponseType, instance } from './api';


type SavePhotoResponseDataType={
	photos : PhotosType
}
/** Server Request Group for Profile*/
export const ProfileAPI = {
	getProfile(userId: number) {
		return instance.get<ProfileType>(`profile/` + userId).then(res => res.data)
	},
	getStatus(userId: number) {
		return instance.get<string>(`profile/status/` + userId).then(res => res.data)
	},
	updateStatus(status: string) {
		return instance.put<APIResponseType>(`profile/status`, { status: status }).then(res => res.data)
	},

	savePhoto(photoFile: File) {   //"File" from DOM-object
		const formData = new FormData();
		formData.append("image", photoFile)
		/** Typing the "put" request for "savePhot". 
		 * This is a standard response type "APIResponseType" with a specification about the photo "<PhotosType>""*/
		return instance.put<APIResponseType<SavePhotoResponseDataType>>(`profile/photo`,
			/** The second parameter changes the type of the file being sent*/
			formData,
			/** The 3 parameter configures the headers of this request*/
			{
				headers: {
					'Content-Type': 'multipart/form-data'
				}
			}).then(res => res.data);
	},

	saveProfile(profile: ProfileType) {
		return instance.put<APIResponseType>(`profile`, profile).then(res => res.data);
	},
}