import {applyMiddleware, combineReducers, legacy_createStore as createStore, compose, Action, AnyAction} from 'redux'; 
import profileReducer from './profile-reducer';
import dialogsReducer from './dialogs-reducer';
import sidebarReducer from './sidebar-reducer';
import usersReducer from './users-reducer';
import authReducer from './auth-reducer';
import appReducer from './app-reducer';
import {thunk, ThunkAction, ThunkDispatch } from 'redux-thunk'; // Отличие от видео
import { reducer as formReducer } from 'redux-form';


let rootReducer = combineReducers({
	profilePage : profileReducer,
	dialogsPage : dialogsReducer,
	sidebar : sidebarReducer,
	usersPage : usersReducer,
	auth : authReducer,
	form : formReducer,
	app : appReducer,
});

/** typeof puts in "RootReducerType" typed sub-items such as: (profilePage, ...) from State(AppState)
 * ReturnType determines what is in <RootReducerType> and returns it to AppStateType
 * "state:" now contains a fully typed state */
type RootReducerType = typeof rootReducer; 				
export type AppStateType = ReturnType<RootReducerType> 
let state : AppStateType; 									


/** Common TYPE "InferActionsTypes" for all reducers */
// "T extends(...)" - If "Т" is "(...)"
type PropertiesTypes<T> = T extends {[key:string]: infer U} ? U : never // "infer" - определяет тип "U" - AC-тора 

/** InferTypes all "actions". Returns everything that functions_from_actions return in {} with the types of theseReturns */
export type InferActionsTypes<T extends {[key:string]: (...args: any[])=>any}> = ReturnType<PropertiesTypes<T>> //"<T extends..." - ограничение для передаваемого "Т"

/** Typing ThunkCreators */
/**  Common type "BaseThunkType" for all ThunkCreators*/
/**  A - ActionsTypes - Actions that can be dispatched from a thunk (A extends Action are Actions from Redux)*/
/**  R - what the function returns (Promise<void> - by default - nothing)
 * AppStateType - all State
 * unknown - extra parameters
 * ThunkAction - function from "redux-thunk"
*/
export type BaseThunkType<A extends Action, R =Promise<void>> = ThunkAction<R, AppStateType, unknown, A>

/**  TYPE CAST for "UseDispatch" due to Redux changes*/
export type AppDispatch = ThunkDispatch<AppStateType, unknown, Action>


/** For Redux Dev Tools
 * we are implementing the thunkMiddleware middleware layer in the store*/

//@ts-ignore
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(rootReducer, composeEnhancers(applyMiddleware(thunk)
));

//@ts-ignore
window.Storage = store;
export default store;