/** BLL - Business Logic Layer
 * Common logic for an app */

import {getAuthUserData} from './auth-reducer';
import { InferActionsTypes } from './redux-store';


let initialState = {
	initialized: false,
};
export type InitialStateType = typeof initialState

/** Automatically infer action types (InferActionsTypes is a common type from "redux-store.ts")*/
type ActionsType = InferActionsTypes<typeof actions>


const appReducer = (state = initialState, action:any) : InitialStateType =>{
	switch(action.type){
		
		case 'app/INITIALIZED_SUCCESS':

				return {
					...state,
					initialized : true,
				};

		default:
			return(state);
	}
}; 
export default appReducer;


/** ActionCreator  for  Post
 * All ACs are packed into 1 Object "actions"
*/
export const actions = {
	initializedSuccess : () =>({type : 'app/INITIALIZED_SUCCESS'} as const)   //Даем  названия const-ам в соотв-и с  Redux-Ducks "app/..."
}


/** THUNK Creator*/
export const  initializeApp = ()=>(dispatch:any)=>{
	let promise = dispatch(getAuthUserData());
	promise.then(( )=> {
		dispatch(actions.initializedSuccess());
	}) 
};


