/** BLL - Business Logic Layer 
 * describes the changes to the State for the Dialogs page */
import { InferActionsTypes } from "./redux-store"


type DialogType = { 
	id : number
	name : string
}
type MessageType = { 
	id : number
	message : string
}
export type InitialStateType = typeof initialState;

let initialState = {
	dialogsData: [
		{ id: 1, name: "Ola" },
		{ id: 2, name: 'Masha' },
		{ id: 3, name: 'Katy' },
		{ id: 4, name: 'Lula' },
		{ id: 5, name: 'Berta' },
		{ id: 6, name: 'Boska' }
	] as Array <DialogType>,  
	messagesData: [
		{ id: 1, message: 'Hallo' },
		{ id: 2, message: 'Tchudfg' },
		{ id: 3, message: 'Gut' }
	] as Array <MessageType>,  
};


/**  Pack all ACs into 1 Object "actions"*/
export const actions = {
	addMessageActionCreator : (newMessageText : string)=>{return {type : 'ADD-MESSAGE', newMessageText} as const
	}
}

type ActionsType = InferActionsTypes<typeof actions>	// InferТипизирует все "actions". Возвращает все то, что ф-ции_Из_аctions возвращают в {} с типамиЭтихВозвратов
export type AddMessageActionCreatorType = typeof actions.addMessageActionCreator	// Типизируем все "addMessageActionCreator"



const dialogsReducer = (state = initialState, action : ActionsType) : InitialStateType=>{

	switch(action.type){
		case 'ADD-MESSAGE':
			let newMessage = {
				id: 4, 
				message: action.newMessageText
			};
		
			return {
				...state,
				messagesData : [...state.messagesData,newMessage]
			};

			default:
			return state;
	}
};
export default dialogsReducer;
	